class SearchInput extends React.Component{
    constructor(props){
        super(props);
        this.item = props.item;
    }
    render(){
        return (
        <div className="searchFields" style={{boxShadow: "rgb(217, 217, 217) 0px 0px 2px", marginRight: "10px", marginBottom: "10px", padding: "10px 15px 10px 15px", backgroundColor: "white"}}>
            <select style={{width: "100px", marginRight: "0px", border: "none", color: "#284b63"}}>
                <option value="lemma">Lemma</option>
                <option value="type">Typ</option>
                <option value="id">ID</option>
                <option value="opus">Werk</option>
            </select>
            <select style={{width: "40px", marginRight: "0px", border: "none", color: "#284b63"}}>
                <option value="=">=</option>
                <option value="!="></option>
                <option value=">">&gt;</option>
                <option value=">="></option>
                <option value="<">&lt;</option>
                <option value="<="></option>
            </select>
            <input type="text" placeholder="..."  style={{width: "100px", marginRight: "22px", border:"none"}} />
            <FontAwesomeIcon color="LightGray"icon={faMinusCircle} onClick={
                () => {this.props.removeSearchFields(this.props.item.id)}} />
        </div>
        );
    }
}
class SearchBox extends React.Component{
    constructor(props){
        super(props);
        this.queryTxt = "";
        this.state = {nextID: 1, searchFields: [{id: 0, c:"lemma", o:"=", v:""}]};
        this.sOrder = null;
    }
    render(){
        const searchBox ={
            boxShadow: "0 0 2px #d9d9d9",
            backgroundColor: "rgb(253, 253, 253)",
            margin: "0 -20px 0 -20px",
            padding: "12px 24px",
            display: "flex",
            flexWrap: "wrap",
            alignItems: "center"
        };
        let cSF = [];
        for(const sF of this.state.searchFields){
            cSF.push(<SearchInput removeSearchFields={id => {this.removeSearchFields(id)}} item={sF} key={sF.id} />);
        }
        return <div style={{gridArea: this.props.gridArea}}>
            <div style={searchBox}>
                {cSF}
                <FontAwesomeIcon color="LightGray" icon={faPlusCircle} style={{color: "var(--mainColor)", backgroundColor: "var(--mainBG)", position: "relative", top: "-5px", fontSize: "25px"}}  onClick={() => {this.addSearchFields()}}
                 />
            </div>
            <div style={{float: "right", marginTop: "10px", marginRight: "20px"}}>
            <input type="button" value="suchen" onClick={() => {this.sendQuery()}} />
            <span style={{marginLeft: "50px", marginRight: "10px"}} className="minorTxt">sortieren:</span><SelectMenu style={{display: "inline-block", width: "100px"}}options={[['["id"]', "ID"], ['["lemma", "lemma_nr", "date_sort", "date_type"]', "Datum"]]} onChange={event=>{this.sOrder = JSON.parse(event.target.value)}} />
            </div>
        </div>
    }

    removeSearchFields(id){
        const nSearchFields = this.state.searchFields.filter(s => s.id!==id);
        this.setState({searchFields: nSearchFields});
    }
    addSearchFields(){
        let nSearchFields = this.state.searchFields;
        nSearchFields.push({
            id: this.state.nextID,
            c: "lemma",
            o: "=",
            v: ""
        });
        this.setState({nextID: (this.state.nextID+1), searchFields: nSearchFields});
    }
    sendQuery(){
        let exportSF = [];
        const searchFields = document.querySelectorAll(".searchFields");
        for(const sF of searchFields){
            if(sF.children[2].value !== ""){
                exportSF.push({
                    c: sF.children[0].value,
                    o: sF.children[1].value,
                    v: sF.children[2].value
                });
            }
        }
        if(exportSF.length > 0){this.props.searchQuery(exportSF, this.sOrder)} 
        else{alert("Geben Sie einen Suchtext ein!")}
    }
}

class Zettel extends React.Component{
    constructor(props){
        super(props);
        this.state = {count:0, selectionDetail:{ids:[], currentId:null}};
    }

    render(){
        return (
        <div style={{padding: "0 10px", display: "grid", gridTemplateColumns: "auto 420px", gridTemplateRows: "min-content auto", rowGap: "15px"}}>
            <SearchBox searchQuery={(q,order) => {this.searchQuery(q,order)}}
            gridArea={(this.state.selectionDetail.ids.length>0)?"1/1/1/2":"1/1/1/3"} />
            <ZettelBox
                loadPage={move => {this.loadPage(move)}}
                currentElements={this.state.currentElements}
                count={this.state.count}
                currentPage={this.state.currentPage}
                maxPage={this.state.maxPage}
                gridArea={(this.state.selectionDetail.ids.length>0)?"2/1/2/2":"2/1/2/3"}
                showDetail={item => {
                    this.setState({selectionDetail: item.selection, itemDetail: item.item});
                }}
            />
            {(this.state.selectionDetail.ids.length>0)?<ZettelAside status={this.props.status} selection={this.state.selectionDetail}item={this.state.itemDetail} onUpdate={ids=>{this.reloadZettel(ids)}} />:""}
        </div>
        );
    }
    async reloadZettel(ids){
        //await this.loadPage(0); // removes entry, if it doesnt match query!
        for(const id of ids){
            let newItem = await arachne.zettel.get({id: id}); newItem = newItem[0];
            let currentElements = this.state.currentElements;
            const indexOfNewItem = currentElements.findIndex(i => i.id===newItem.id)
            currentElements[indexOfNewItem] = newItem;
            this.setState({currentElements: currentElements, selectionDetail: {ids:ids}, itemDetail: newItem});
        }
    }
    async searchQuery(newQuery, order){
        this.props.status("searching");
        const count = await arachne.zettel.search(newQuery, {count:true, order:order});
        const currentElements = await arachne.zettel.search(newQuery, {limit:50, order:order});
        console.log(count);
        if(count[0]["count"]>1){this.props.status("found", `${count[0]["count"]} Eintrge gefunden.`)}
        else if(count[0]["count"]===1){this.props.status("found", "1 Eintrag gefunden.")}
        else{this.props.status("notFound")}
        this.setState({
            query: newQuery,
            queryOrder: order,
            count: count[0]["count"],
            maxPage: Math.floor(count[0]["count"]/50)+1,
            currentPage: 1,
            currentElements: currentElements,
            selectionDetail: {ids:[]}
        });
    }
    async loadPage(move){
        if(this.state.currentPage+move>0&&this.state.currentPage+move<=this.state.maxPage){
            const currentElements = await arachne.zettel.search(this.state.query, {limit:50, offset:((this.state.currentPage+move-1)*50), order:this.state.queryOrder});
            this.setState({
                currentPage: this.state.currentPage+move,
                currentElements: currentElements,
                selectionDetail: {ids:[]}
            });
        }
    }
}
class ZettelBox extends React.Component{
    constructor(props){
        super(props);
        this.state = {selection: {currentId: null, ids:[]}}
    }
    render(){
        if(this.props.count>0){
            let cEls = [];
            for(const cEl of this.props.currentElements){
                cEls.push(<ZettelCard testProp={cEl.id+"_test"} id={cEl.id} item={cEl} key={cEl.id} />);
            }

            return (
            <div style={{gridArea: this.props.gridArea, padding:"0 10px"}}>
                <Navigator loadPage={move=>{this.props.loadPage(move)}}currentPage={this.props.currentPage} maxPage={this.props.maxPage} />
                <Selector multiSelect={true} className="zettel_box" selectCallback={
                    (item, selection)=>{this.selectCallback(item, selection)}
                } >{cEls}</Selector>
            </div>);
        } else {
            return null;
        }
    }
    selectCallback(element, selection){
        if(element){this.props.showDetail({selection: selection, item: element.props.item});}
        else{this.props.showDetail({selection: selection, item: null});};
    }
}
